.gpt3__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gpt3__footer-card {
    
    display: flex;
    /*justify-content: space-between;*/
    
    max-width: 1050px;
    padding: 2rem;
    border-radius: 10px;
}

.gpt3__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;

    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt3__footer-btn p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
    max-width: 1250px;
}

.gpt3__footer-links img{
    width: 35px;
    height: 35px;
    align-content: right;
    align-items: flex-end;
    text-align: right;
}

.gpt3__footer-links div {
    width: 250px;
    margin: 1rem;
}

.gpt3__footer-links_logo {
    display: flex;
    flex-direction: row;
    
}

.gpt3__footer-links_logo img {
    width: 25px;
    height: 25px;
    margin-left: 5px;
}

.gpt3__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 25px;
    color: var(--color-text);
    
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    display: inline-block;
    text-align: right;
    align-content: right;
    
}

.gpt3__footer-links_div img {
    width: 25px;
    height: 25px;
    align-content: right;
    align-items: flex-end;
    text-align: right;
    vertical-align: middle;
    margin: 0.5rem;
}

.gpt3__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.gpt3__footer-links_div p {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__footer-links div {
        margin: 1rem 0;
    }

    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 755px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }

    .gpt3__footer-links_div {
       /* justify-content: flex-end;
    display: inline-block;*/
    text-align: left;
    align-content: left;
    }
}
